<template>
  <div id="app">
    <div id="nav" class="text-right pr-2 text-white text-sm">
      <span v-show="user != ''">Bienvenue {{ user.username }}</span>
      <router-link to="/login" v-show="user == ''">Connexion</router-link>
      <a @click="logOut" class="span-link" v-show="user != ''">Déconnexion</a>
    </div>
    <router-view/>
  </div>
</template>

<script>
// import pictures from '@/pictures.json';
import axios from 'axios';

export default {
  name: 'App',
  data() {
    return {
      user: '',
    };
  },
  created() {
    this.isAuthenticated();
  },
  methods: {
    isAuthenticated() {
      axios.get('http://localhost:3000/api/user', { withCredentials: true }).then((response) => {
        if (response.data.success !== false) {
          this.user = response.data.user;
        }
      });
    },
    logOut() {
      axios.get('http://localhost:3000/api/logout', { withCredentials: true }).then(() => {
        this.$router.push('/');
      });
    },
  },
};
</script>

<style scoped>
.span-link:hover{
  cursor:pointer;
}
</style>
